import { toRefs, computed } from "vue";
import { t } from "@nextcloud/l10n";
import Message from "./Message/Message.vue";
import AvatarWrapper from "../../AvatarWrapper/AvatarWrapper.vue";
import { useMessageInfo } from "../../../composables/useMessageInfo.js";
import { ATTENDEE, AVATAR } from "../../../constants.js";
import { useGuestNameStore } from "../../../stores/guestName.js";
export default {
  name: "MessagesGroup",
  components: {
    AvatarWrapper,
    Message
  },
  inheritAttrs: false,
  props: {
    /**
     * The conversation token.
     */
    token: {
      type: String,
      required: true
    },
    /**
     * The messages object.
     */
    messages: {
      type: Array,
      required: true
    },
    previousMessageId: {
      type: [String, Number],
      default: 0
    },
    nextMessageId: {
      type: [String, Number],
      default: 0
    }
  },
  setup(props) {
    const { messages } = toRefs(props);
    const firstMessage = computed(() => messages.value[0]);
    const {
      remoteServer,
      lastEditor,
      actorDisplayName,
      actorDisplayNameWithFallback
    } = useMessageInfo(firstMessage);
    const actorInfo = computed(() => {
      return [actorDisplayNameWithFallback.value, remoteServer.value, lastEditor.value].filter((value) => value).join(" ");
    });
    return {
      AVATAR,
      guestNameStore: useGuestNameStore(),
      actorDisplayName,
      actorInfo
    };
  },
  computed: {
    actorId() {
      return this.messages[0].actorId;
    },
    actorType() {
      return this.messages[0].actorType;
    },
    disableMenu() {
      return this.$store.getters.isActorGuest() || this.actorType === ATTENDEE.ACTOR_TYPE.BRIDGED;
    }
  },
  methods: {
    t
  }
};
