    },
    reactionsSorted() {
      if (this.detailedReactions) {
        return Object.keys(this.detailedReactions).sort((a, b) => this.detailedReactions[b].length - this.detailedReactions[a].length);
      } else if (this.plainReactions) {
        return Object.keys(this.plainReactions).sort((a, b) => this.plainReactions[b] - this.plainReactions[a]);
      }
      return void 0;
    },
