import { loadState } from "@nextcloud/initial-state";
import NcUserBubble from "@nextcloud/vue/dist/Components/NcUserBubble.js";
import { useIsDarkTheme } from "@nextcloud/vue/dist/Composables/useIsDarkTheme.js";
import { MENTION } from "../../../../../constants.js";
import { getConversationAvatarOcsUrl, getUserProxyAvatarOcsUrl } from "../../../../../services/avatarService.ts";
export default {
  name: "Mention",
  components: {
    NcUserBubble
  },
  props: {
    token: {
      type: String,
      required: true
    },
    type: {
      type: String,
      required: true
    },
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    server: {
      type: String,
      default: ""
    }
  },
  setup() {
    const isDarkTheme = useIsDarkTheme();
    return {
      isDarkTheme
    };
  },
  data() {
    return {
      size: null
    };
  },
  computed: {
    isMentionToAll() {
      return this.type === MENTION.TYPE.CALL;
    },
    isGroupMention() {
      return [MENTION.TYPE.USERGROUP, MENTION.TYPE.GROUP].includes(this.type);
    },
    isMentionToGuest() {
      return this.type === MENTION.TYPE.GUEST || this.type === MENTION.TYPE.EMAIL;
    },
    isRemoteUser() {
      return [MENTION.TYPE.USER, MENTION.TYPE.FEDERATED_USER].includes(this.type) && this.server !== "";
    },
    isCurrentGuest() {
      return this.$store.getters.isActorGuest() && (this.id === "guest/" + this.$store.getters.getActorId() || this.id === this.$store.getters.getActorId());
    },
    isCurrentUser() {
      if (this.isRemoteUser) {
        return false;
      }
      return this.$store.getters.isActorUser() && this.id === this.$store.getters.getUserId();
    },
    isCurrentUserGroup() {
      return this.isGroupMention && loadState("spreed", "user_group_ids", []).includes(this.id);
    },
    primary() {
      return this.isMentionToAll || this.isCurrentUser || this.isGroupMention && this.isCurrentUserGroup || this.isMentionToGuest && this.isCurrentGuest;
    },
    avatarUrl() {
      if (this.isRemoteUser) {
        return this.token ? getUserProxyAvatarOcsUrl(this.token, this.id + "@" + this.server, this.isDarkTheme, 64) : "icon-user-forced-white";
      } else if (this.isGroupMention) {
        return "icon-group-forced-white";
      } else if (this.isMentionToGuest) {
        return "icon-user-forced-white";
      } else if (!this.isMentionToAll) {
        return void 0;
      }
      return getConversationAvatarOcsUrl(this.id, this.isDarkTheme);
    }
  },
  mounted() {
    this.size = parseInt(window.getComputedStyle(this.$refs.mention).fontSize, 10) * 4 / 3;
  }
};
