import { t } from "@nextcloud/l10n";
export default {
  name: "DeckCard",
  props: {
    type: {
      type: String,
      required: true
    },
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    boardname: {
      type: String,
      required: true
    },
    stackname: {
      type: String,
      required: true
    },
    link: {
      type: String,
      required: true
    },
    wide: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    deckLocation() {
      return t("spreed", "{stack} in {board}", {
        stack: this.stackname,
        board: this.boardname
      });
    },
    deckCardAriaLabel() {
      return t("spreed", "Deck Card");
    }
  },
  methods: {
    t
  }
};
