"use strict";
function getLargestIndexes(array) {
  let maxValue = 0;
  const maxIndexes = [];
  for (let i = 0; i < array.length; i++) {
    if (array[i] > maxValue) {
      maxValue = array[i];
      maxIndexes.length = 0;
      maxIndexes.push(i);
    } else if (array[i] === maxValue) {
      maxIndexes.push(i);
    }
  }
  return maxIndexes;
}
export function calculateVotePercentage(votes, total) {
  if (!total) {
    return votes;
  }
  const rounded = [];
  const wholes = [];
  const remainders = [];
  let sumRounded = 0;
  let sumWholes = 0;
  for (const i in votes) {
    const quota = votes[i] / total * 100;
    rounded.push(Math.round(quota));
    wholes.push(Math.floor(quota));
    remainders.push(Math.round(quota % 1 * 1e3));
    sumRounded += rounded[i];
    sumWholes += wholes[i];
  }
  if (sumRounded === 100) {
    return rounded;
  }
  for (let i = 100 - sumWholes; i > 0; ) {
    const largest = getLargestIndexes(remainders);
    if (largest.length > i) {
      return wholes;
    }
    for (const idx of largest) {
      wholes[idx]++;
      remainders[idx] = 0;
      i--;
    }
  }
  return wholes;
}
