import {
  LControlAttribution,
  LTooltip,
  LMap,
  LMarker,
  LTileLayer
} from "vue2-leaflet";
import { t } from "@nextcloud/l10n";
export default {
  name: "Location",
  components: {
    LControlAttribution,
    LTileLayer,
    LMap,
    LMarker,
    LTooltip
  },
  props: {
    /**
     * The latitude of the location
     */
    latitude: {
      type: Number,
      required: true
    },
    /**
     * The longitude of the location
     */
    longitude: {
      type: Number,
      required: true
    },
    /**
     * The name of the location
     */
    name: {
      type: String,
      default: ""
    },
    wide: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      // The zoom level of the map in the messages list
      previewZoom: 13,
      // The zoom level of the map in the new openstreetmap tab upon
      // Opening the link
      linkZoom: 18,
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    };
  },
  computed: {
    center() {
      return [this.latitude, this.longitude];
    },
    mapLink() {
      return `https://www.openstreetmap.org/?mlat=${this.latitude}&mlon=${this.longitude}#map=${this.linkZoom}/${this.latitude}/${this.longitude}`;
    },
    linkAriaLabel() {
      return t("spreed", "Open this location in OpenStreetMap");
    }
  },
  methods: {
    t
  }
};
